
.movieList {
  padding-top: 38px;
}
.movieList li {
  width: 100%;
  height: 170px;
  padding: 5px;
}
.images {
  float: left;
  margin-right: 5px;
}
.images img {
  width: 100px;
  height: 140px;
}
.info div {
  margin: 5px 0;
}
.info .actor span,
.info .type span {
  margin-right: 5px;
  color: #808080;
}
.more {
  float: right;
}


