
.itemBox {
  padding-top: 38px;
}
.itemBox li {
  padding: 8px;
}
.item {
  width: 100%;
  position: relative;
}
.item img {
  width: 100%;
  border-radius: 5px;
}
.item a {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: transparent;
  z-index: 100;
}
.item div {
  background: rgba(0,0,0,0.5);
  color: #fff;
  width: 100%;
  height: 50px;
  position: absolute;
  left: 0px;
  bottom: 0px;
  border-radius: 5px;
  padding: 5px;
  line-height: 20px;
}
.item div p {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.item div p span {
  margin-right: 5px;
}


