
.bookBox {
  padding-top: 38px;
}
.bookList {
  width: 100%;
}
.bookList li {
  width: 50%;
  display: inline-block;
  padding: 5px;
}
.bookList li img {
  width: 100%;
  height: 280px;
  border: solid 1px #ccc;
  border-radius: 5px;
}
.bookList .bookname {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.bookList div {
  margin: 3px 0px;
}
.bookList .summary {
  position: relative;
  line-height: 20px;
  max-height: 40px;
  overflow: hidden;
}
.bookList .summary::after {
  content: "...";
  position: absolute;
  bottom: 0;
  right: 0;
  padding-left: 40px;
  background: -webkit-linear-gradient(left, transparent, #fff 55%);
  background: linear-gradient(to right, transparent, #fff 55%);
}
.bookList .more {
  float: right;
}


