
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
figcaption,
figure,
fieldset,
legend,
button,
input,
textarea,
th,
td {
  margin: 0;
  padding: 0;
}
body,
button,
input,
select,
textarea {
  font: 12px/1 Lucida Grande, 'Microsoft YaHei', "Lucida Grande", "Lucida Sans Unicode", Helvetica, Arial, Verdana, sans-serif;
}
h1 {
  font-size: 18px;
  font-weight: normal;
}
h2 {
  font-size: 16px;
  font-weight: normal;
}
h3 {
  font-size: 14px;
  font-weight: normal;
}
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
address,
cite,
dfn,
em,
var {
  font-style: normal;
}
code,
kbd,
pre,
samp,
tt {
  font-family: "Courier New", Courier, monospace;
}
small {
  font-size: 12px;
}
ul,
ol,
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #333;
}
a:active {
  color: #333;
}
abbr[title],
acronym[title] {
  border-bottom: 1px dotted;
  cursor: help;
}
q:before,
q:after {
  content: '';
}
legend {
  color: #000;
}
fieldset,
img {
  border: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
hr {
  border: 0;
  height: 1px;
}
* {
  -ms-word-break: break-all;
  word-break: break-all;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  box-sizing: border-box;
}
.router-link-active {
  color: #f00;
}
html,
body {
  height: 100%;
}
.#app {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}
.menu {
  width: 120px;
  height: 100%;
  position: fixed;
  z-index: 200;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.5);
  overflow-y: scroll;
  padding-top: 60px;
}
::-webkit-scrollbar {
  display: none;
}
.menu>div {
  width: 100%;
  height: 40px;
  line-height: 40px;
}
.menu>div a {
  display: block;
  width: 100%;
  height: 100;
  color: #999;
  font-size: 18px;
  padding-left: 17px;
}
.menu>div a .iconfont {
  margin-right: 4px;
}
.menu-icon {
  width: 40px;
  height: 40px;
  position: fixed;
  z-index: 240;
  left: 15px;
  top: 34px;
  font-size: 24px;
  color: #888;
}
.main {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}


