
.tab {
  width: 100%;
  height: 36px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 36px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 300;
  background: #efefef;
}
.tab li {
  color: #333;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  text-align: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.tab li a {
  display: block;
}
.slide-enter-active {
  -webkit-transition: all 0.5s ease cubic-bezier(0.68, -0.55, 0.27, 1.55);
  transition: all 0.5s ease cubic-bezier(0.68, -0.55, 0.27, 1.55);
}
.slide-leave-active {
  -webkit-transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-enter,
.slide-leave-active {
  -webkit-transform: translateX(150px);
          transform: translateX(150px);
  opacity: 0;
}


